@mixin make-link-02() {
  text-decoration: underline;
  width: fit-content;
}

@mixin make-link-02-modifier(
  $hover-text-color: false,
  $text-color: false,
) {
  @if $text-color {
    color: $text-color;
  }
  transition: color $ease-standard-a $time-b;
  @media (hover: hover) {
    &:hover,
    &:focus,
    &:active {
      @if $hover-text-color {
        color: $hover-text-color;
      }
    }
  }
}
